.agencia-card {
    border: 1px solid #ccc;
    padding: 16px;
    margin-bottom: 16px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.agencia-card h2 {
    margin: 0 0 8px;
}

.agencia-card p {
    margin: 4px 0;
}

.agencia-photo {
    max-width: 200px;
    margin-left: 16px;
}